.contact-form {
  display: block;
  margin: 20px auto;
  background: #ccc;
}

.form {
  margin: 20px auto;
  width: 70%;
  text-align: left;
}

.label {
  font-size: 16px;
  margin-top: 20px;
  line-height: 21px;
  color: #333;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
}

.label input,
.label textarea {
  display: block;
  border: 1px solid #249;
  padding: 6px 8px;
  margin: 10px 0;
}



